
html,
body,
#app {
  height: 100%;
}
.hd {
  display: none;
}
.setup {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: #95cb75;
  z-index: 9999;
  top: 0;
  left: 0;
}
.setup .prog {
  background-color: #fff;
  position: absolute;
  width: 100%;
  height: 100%;
  margin: auto;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}
.setup .prog span {
  font-size: 20px;
  font-weight: bold;
  position: absolute;
  background-color: #fff;
  text-align: center;
  width: 100%;
  height: 100px;
  border-radius: 50%;
  margin: auto;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  line-height: 100px;
}
.setup {
  animation: anim-setup 0.7s linear 1.5s 1 normal;
  animation-play-state: running;
  animation-fill-mode: none;
  -webkit-animation: anim-setup 0.7s linear 1.5s 1 normal;
  -webkit-animation-play-state: running;
  -webkit-animation-fill-mode: none;
  -moz-animation: anim-setup 0.7s linear 1.5s 1 normal;
  -moz-animation-play-state: running;
  -moz-animation-fill-mode: none;
  -o-animation: anim-setup 0.7s linear 1.5s 1 normal;
  -o-animation-play-state: running;
  -o-animation-fill-mode: none;
  -ms-animation: anim-setup 0.7s linear 1.5s 1 normal;
  -ms-animation-play-state: running;
  -ms-animation-fill-mode: none;
}
.setup .prog {
  animation: anim-prog 1.5s ease-out 0s 1 normal;
  animation-play-state: running;
  animation-fill-mode: none;
  -webkit-animation: anim-prog 1.5s ease-out 0s 1 normal;
  -webkit-animation-play-state: running;
  -webkit-animation-fill-mode: none;
  -moz-animation: anim-prog 1.5s ease-out 0s 1 normal;
  -moz-animation-play-state: running;
  -moz-animation-fill-mode: none;
  -o-animation: anim-prog 1.5s ease-out 0s 1 normal;
  -o-animation-play-state: running;
  -o-animation-fill-mode: none;
  -ms-animation: anim-prog 1.5s ease-out 0s 1 normal;
  -ms-animation-play-state: running;
  -ms-animation-fill-mode: none;
}
@keyframes anim-setup {
0% {
    opacity: 0.8;
}
100% {
    opacity: 0;
}
}
@keyframes anim-prog {
0% {
    width: 100%;
    height: 100px;
}
100% {
    width: 100%;
    height: 100%;
}
}



